/*Обнуление*/
/*----------------------------------------------------------------------------*/
* {
  padding: 0;
  margin: 0; /* обнуление всех отступор на странице и всех тегов */
  border: 0;
}
/*----------------------------------------------------------------------------*/
*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box; /* изменение подсчета ширины блока для удобства верстания */
  box-sizing: border-box;
}
/*----------------------------------------------------------------------------*/
:focus,
:active {
  outline: none;
} /* уберает линии обводки у некоторых браузеров */
a:focus,
a:active {
  outline: none;
}
/*----------------------------------------------------------------------------*/
nav,
footer,
header,
aside {
  display: block;
} /* переводит в блочный тип */
/*----------------------------------------------------------------------------*/
html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1; /* уравнивает поведение шрифтов и элементов для разных браузеров (запрещает самовольное увеличение шрифтов напр. Iphone)  */
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
/*----------------------------------------------------------------------------*/
input,
button,
textarea {
  font-family: inherit;
} /* наследование семейства шрифтов */
/*----------------------------------------------------------------------------*/
input::-ms-clear {
  display: none;
}
button {
  cursor: pointer;
}
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}
a,
a:visited {
  text-decoration: none;
} /* коротко-уберае некоторые особенности в разных браузеров */
a:hover {
  text-decoration: none;
}
ul li {
  list-style: none;
}
img {
  vertical-align: top;
}
/*----------------------------------------------------------------------------*/
h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: 400;
}
/*----------------------------------------------------------------------------*/

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  background-image: radial-gradient(
    circle farthest-corner at 10% 20%,
    rgba(0, 152, 155, 1) 0.1%,
    rgba(0, 94, 120, 1) 94.2%
  );
  background-attachment: fixed;
}
h1 {
  font-size: 55px;
  font-weight: 200;
  margin: 20px 0px 20px -50px;
  text-align: center;
}
a {
  color: #000;
  margin: 20px 0px 20px 30px;
}
input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
.flag-usd {
  background-position: 0px 240px;
}
.row {
  display: flex;
  justify-content: center;
}
.row__column {
  display: flex;
  flex-direction: column;
}
.form-control {
  width: 360px;
  height: 50px;
  font-size: 35px;
  font-weight: 300;
  border-radius: 10px;
  padding: 0px 70px 5px 10px;
  margin: 15px 16px;
  color: rgba(8, 24, 12, 0.863);
  background-image: radial-gradient(
    circle 592px at 48.2% 50%,
    rgba(255, 255, 249, 0.6) 0%,
    rgba(160, 199, 254, 1) 74.6%
  );
}
.form-control:hover {
  border: 2px solid rgb(66, 164, 177);
}

.container__convertor-box {
  margin: 0 auto;
  width: 50%;
}
.currency {
  position: relative;
  top: 30px;
  right: 100px;
  height: 25px;
}
.country-flag {
  background-image: url(../img/flags.png);
  width: 15px;
  height: 15px;
  margin: 3px;
}
.val-name {
  font-size: 20px;
  color: rgb(129, 129, 129);
}
.add-convertor__box {
  width: 160px;
  height: 30px;
  border-radius: 12px;
  color: rgb(129, 129, 129);
  font-size: 14px;
  margin-left: 30px;
  text-transform: uppercase;
  font-weight: 200;

}

#message:empty {
  opacity: 0;
}

#message {
  color: white;
  padding: 1rem;
  margin: 1rem;
  background-color: red;
}
